import React, { useState, useEffect, useContext } from "react";
import "semantic-ui-css/semantic.min.css";
import "./App.css";
import bocadillo from "./images/bocadillo.png";
import chocolate from "./images/chocolate.png";
import coca from "./images/coca.png";
import cena from "./images/cena.png";
import imageCompression from "browser-image-compression";
import { storage } from "./firebase/firebase";

import { Carrito } from "./Contexto";
import {
  Container,
  Segment,
  Header,
  Statistic,
  Table,
  List,
  Popup,
  Image,
  Label,
  Button,
  Icon,
  Modal,
  Dimmer,
  Loader,
  Input,
  Divider,
  Dropdown,
} from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";

function currencyFormat(num) {
  return "$" + num.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
}
function removeDuplicates(myArr, prop) {
  return myArr.filter((obj, pos, arr) => {
    return arr.map((mapObj) => mapObj[prop]).indexOf(obj[prop]) === pos;
  });
}
function makeid(length) {
  var result = "";
  var characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}

function compare(a, b, sort_key) {
  // Use toUpperCase() to ignore character casing
  const fechaA = a[sort_key];
  const fechaB = b[sort_key];

  let comparison = 0;
  if (fechaA > fechaB) {
    comparison = -1;
  } else if (fechaA < fechaB) {
    comparison = 1;
  }
  return comparison;
}

function ExcelDateToJSDate(serial) {
  var utc_days = Math.floor(serial - 25568);
  var utc_value = utc_days * 86400;
  var date_info = new Date(utc_value * 1000);

  var fractional_day = serial - Math.floor(serial) + 0.0000001;

  var total_seconds = Math.floor(86400 * fractional_day);

  var seconds = total_seconds % 60;

  total_seconds -= seconds;

  var hours = Math.floor(total_seconds / (60 * 60));
  var minutes = Math.floor(total_seconds / 60) % 60;

  var date_data = new Date(
    date_info.getFullYear(),
    date_info.getMonth(),
    date_info.getDate(),
    0,
    0,
    0
  );

  return date_data;
}

function FechaString(fecha) {
  var ye = new Intl.DateTimeFormat("en", { year: "numeric" }).format(fecha);
  var mo = new Intl.DateTimeFormat("en", { month: "short" }).format(fecha);
  var da = new Intl.DateTimeFormat("en", { day: "2-digit" }).format(fecha);
  var fechaStr = `${da}-${mo}-${ye}`;
  return fechaStr;
}

function FechaStringShort(fecha) {
  var ye = new Intl.DateTimeFormat("en", { year: "numeric" }).format(fecha);
  var mo = new Intl.DateTimeFormat("en", { month: "numeric" }).format(fecha);
  var da = new Intl.DateTimeFormat("en", { day: "2-digit" }).format(fecha);
  var fechaStr = `${ye}-${mo < 10 ? "0" + mo : mo}-${da}`;
  return fechaStr;
}

export default function Detalles() {
  //Fechas
  //fechas estan erradas hay que corregir
  let today = new Date();
  let yesterday = new Date(today);
  yesterday.setDate(yesterday.getDate() - 1);
  let wd = today.getDay();
  wd === 0 ? (wd = 7) : (wd = wd);
  const [dataSemana, setDS] = useState();
  const [inicio, setInicio] = useState(FechaStringShort(today));
  const [final, setFinal] = useState(FechaStringShort(today));
  //Fechas
  const [data, setData] = useState();
  const [loading, setLoading] = useState(false);
  const [buscar, setBuscar] = useState("");
  const [cliente, setCliente] = useState("");
  const [listaClientes, setListaClientes] = useState();
  const [listaProd, setListaProd] = useState();
  const [sort_key, setSortKey] = useState("Product_Name");
  const [sort_order, setSortOrder] = useState("ascending");
  const [ttlAmt, setTtlamt] = useState(0);

  function HandleData() {}

  const sacardata = async () => {
    setLoading(true);
    var data = [];
    let datas = [];
    const creds = require("./samsnacks.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1usbvWq_DBOLN4j1wvK-l7QquTHe9yc9PqFRXhhOmZk0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[0];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;
    let lastr = rows.length + 1;
    var j, i;
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        if (encabezado[i] === "Fecha") {
          window["Celda" + j + i] = FechaStringShort(
            ExcelDateToJSDate(window["Celda" + j + i])
          );
        }
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    var helper = {};
    var result = datas.reduce(function (r, o) {
      var key = o.Fecha + "-" + o.Product_Name + "-" + o.Cliente;

      if (!helper[key]) {
        helper[key] = Object.assign({}, o); // create a copy of o
        r.push(helper[key]);
      } else {
        helper[key].Cantidad += o.Cantidad;
        helper[key].Monto += o.Monto;
        helper[key].Payment_Amt += o.Payment_Amt;
      }

      return r;
    }, []);

    setData(result);
    console.log(result);
    setLoading(false);
  };
  function ordenar(a, b) {
    // Use toUpperCase() to ignore character casing
    const fechaA = a[sort_key];
    const fechaB = b[sort_key];

    let comparison = 0;
    if (fechaA < fechaB) {
      sort_order === "ascending" ? (comparison = -1) : (comparison = 1);
    } else if (fechaA > fechaB) {
      sort_order === "ascending" ? (comparison = 1) : (comparison = -1);
    }
    return comparison;
  }

  useEffect(() => {
    !data && sacardata();
    if (data) {
      var temp = removeDuplicates(
        data
          .sort(function ordenar(a, b) {
            // Use toUpperCase() to ignore character casing
            const fechaA = a["Cliente"];
            const fechaB = b["Cliente"];

            let comparison = 0;
            if (fechaA < fechaB) {
              comparison = -1;
            } else if (fechaA > fechaB) {
              comparison = 1;
            }
            return comparison;
          })
          .filter((item) => {
            return item.Fecha >= inicio && item.Fecha <= final;
          }),
        "Cliente"
      );
      let templista = [];
      var i;
      for (i = 0; i < temp.length; i++) {
        templista.push({
          key: temp[i].Cliente,
          value: temp[i].Cliente,
          text: temp[i].Cliente,
        });
      }
      setListaClientes(templista);
      temp = removeDuplicates(
        data
          .sort(function ordenar(a, b) {
            // Use toUpperCase() to ignore character casing
            const fechaA = a["Product_Name"];
            const fechaB = b["Product_Name"];

            let comparison = 0;
            if (fechaA < fechaB) {
              comparison = -1;
            } else if (fechaA > fechaB) {
              comparison = 1;
            }
            return comparison;
          })
          .filter((item) => {
            return item.Fecha >= inicio && item.Fecha <= final;
          }),
        "Product_Name"
      );
      templista = [];
      for (i = 0; i < temp.length; i++) {
        templista.push({
          key: temp[i].Product_Name,
          value: temp[i].Product_Name,
          text: temp[i].Product_Name,
        });
      }
      setListaProd(templista);
    }
  }, [data, inicio, final]);

  useEffect(() => {
    if (data) {
      var i;
      for (i = 0; i < data.length; i++) {
        let result = data
          .filter((item) => {
            return (
              item.Product_Name.toLowerCase().includes(
                buscar ? buscar.toLowerCase() : buscar
              ) &&
              item.Cliente.toLowerCase().includes(
                cliente ? cliente.toLowerCase() : cliente
              ) &&
              item.Fecha >= inicio &&
              item.Fecha <= final
            );
          })
          .reduce((r, d) => r + d.Monto, 0);
        setTtlamt(result);
      }
    }
  }, [data, cliente, buscar, inicio, final]);
  return (
    <Container>
      <Dimmer active={loading} page>
        <Loader>"Loading"</Loader>
      </Dimmer>
      <Header>Detalle de Ventas</Header>
      <Input
        id="inicio"
        label="Inicio"
        size="mini"
        type="date"
        defaultValue={inicio}
        onChange={(e) => {
          setInicio(e.target.value);
        }}
      />
      <Input
        id="final"
        label="Final"
        size="mini"
        type="date"
        defaultValue={final}
        onChange={(e) => {
          setFinal(e.target.value);
        }}
      />
      <Dropdown
        placeholder="Products"
        fluid
        clearable
        search
        selection
        options={listaProd}
        onChange={(e, { value }) => {
          setBuscar(value);
        }}
      />
      <Dropdown
        placeholder="Clientes"
        fluid
        clearable
        search
        selection
        options={listaClientes}
        onChange={(e, { value }) => {
          setCliente(value);
        }}
      />
      {data && (
        <Segment className="contenido">
          <Table basic="very" celled collapsing>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell
                  onClick={() => {
                    setSortKey("Fecha");
                  }}
                >
                  Fecha
                  <Icon
                    name={
                      sort_order === "ascending"
                        ? "triangle up"
                        : "triangle down"
                    }
                    onClick={() => {
                      setSortKey("Fecha");
                      sort_order === "ascending"
                        ? setSortOrder("descending")
                        : setSortOrder("ascending");
                    }}
                  ></Icon>
                </Table.HeaderCell>
                <Table.HeaderCell
                  onClick={() => {
                    setSortKey("Product_Name");
                  }}
                >
                  Producto
                  <Icon
                    name={
                      sort_order === "ascending"
                        ? "triangle up"
                        : "triangle down"
                    }
                    onClick={() => {
                      setSortKey("Product_Name");
                      sort_order === "ascending"
                        ? setSortOrder("descending")
                        : setSortOrder("ascending");
                    }}
                  ></Icon>
                </Table.HeaderCell>
                <Table.HeaderCell
                  onClick={() => {
                    setSortKey("Cliente");
                  }}
                >
                  Cliente
                  <Icon
                    name={
                      sort_order === "ascending"
                        ? "triangle up"
                        : "triangle down"
                    }
                    onClick={() => {
                      setSortKey("Cliente");
                      sort_order === "ascending"
                        ? setSortOrder("descending")
                        : setSortOrder("ascending");
                    }}
                  ></Icon>
                </Table.HeaderCell>
                <Table.HeaderCell
                  onClick={() => {
                    setSortKey("Cantidad");
                  }}
                >
                  Cantidad
                  <Icon
                    name={
                      sort_order === "ascending"
                        ? "triangle up"
                        : "triangle down"
                    }
                    onClick={() => {
                      setSortKey("Cantidad");
                      sort_order === "ascending"
                        ? setSortOrder("descending")
                        : setSortOrder("ascending");
                    }}
                  ></Icon>
                </Table.HeaderCell>
                <Table.HeaderCell
                  onClick={() => {
                    setSortKey("Monto");
                  }}
                >
                  Monto
                  <Icon
                    name={
                      sort_order === "ascending"
                        ? "triangle up"
                        : "triangle down"
                    }
                    onClick={() => {
                      setSortKey("Monto");
                      sort_order === "ascending"
                        ? setSortOrder("descending")
                        : setSortOrder("ascending");
                    }}
                  ></Icon>
                </Table.HeaderCell>
                <Table.HeaderCell
                  onClick={() => {
                    setSortKey("Payment_Amt");
                  }}
                >
                  Pago
                  <Icon
                    name={
                      sort_order === "ascending"
                        ? "triangle up"
                        : "triangle down"
                    }
                    onClick={() => {
                      setSortKey("Payment_Amt");
                      sort_order === "ascending"
                        ? setSortOrder("descending")
                        : setSortOrder("ascending");
                    }}
                  ></Icon>
                </Table.HeaderCell>
              </Table.Row>
            </Table.Header>

            <Table.Body>
              {ttlAmt !== 0 && (
                <Table.Row>
                  <Table.Cell colSpan="4">Total</Table.Cell>
                  <Table.Cell>{currencyFormat(ttlAmt)}</Table.Cell>
                  <Table.Cell></Table.Cell>
                </Table.Row>
              )}
              {data
                .filter((item) => {
                  return (
                    item.Product_Name.toLowerCase().includes(
                      buscar ? buscar.toLowerCase() : buscar
                    ) &&
                    item.Cliente.toLowerCase().includes(
                      cliente ? cliente.toLowerCase() : cliente
                    ) &&
                    item.Fecha >= inicio &&
                    item.Fecha <= final
                  );
                })
                .sort(function ordenar(a, b) {
                  // Use toUpperCase() to ignore character casing
                  const fechaA = a["Monto"];
                  const fechaB = b["Monto"];

                  let comparison = 0;
                  if (fechaA < fechaB) {
                    comparison = 1;
                  } else if (fechaA > fechaB) {
                    comparison = -1;
                  }
                  return comparison;
                })
                .sort(ordenar)
                .map((item) => (
                  <Table.Row>
                    <Table.Cell>{item.Fecha}</Table.Cell>
                    <Table.Cell>{item.Product_Name}</Table.Cell>
                    <Table.Cell>{item.Cliente}</Table.Cell>
                    <Table.Cell>{item.Cantidad}</Table.Cell>
                    <Table.Cell>{currencyFormat(item.Monto)}</Table.Cell>
                    <Table.Cell>{currencyFormat(item.Payment_Amt)}</Table.Cell>
                  </Table.Row>
                ))}
            </Table.Body>
          </Table>
        </Segment>
      )}
    </Container>
  );
}
